  padding: 0.75em;
  text-align: center;
}

.status--status a {
  color: shade(#cfefc2, 70%);
  text-decoration: underline;
}

.status--status a:focus, .status--status a:hover {
  color: shade(#cfefc2, 90%);
}

.status--status .placeholder {
  font-style: normal;
  font-weight: 800;
}

.card__grid {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1em;
}

.card--grid-item {
  flex-grow: 1;
  margin-bottom: 1em;
  width: 100%;
}

@media (min-width: 920px) {
  .card--grid-item {
    margin: 0 3% 3% 0;
    width: auto;
  }
}

.card--grid-item:last-child {
  margin-right: 0;
}

@media (min-width: 920px) {
  .card--grid-item {
    width: 31%;
  }
  .card--grid-item:nth-child(3n) {
    margin-right: 0;
  }
}

.card--divider-3 {
  flex-grow: 0;
}

@media (min-width: 920px) {
  .card--divider-3 {
    margin-right: 3.5%;
    position: relative;
  }
  .card--divider-3::after {
    background-color: #ccc;
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    top: 0;
    width: 1px;
  }
}

.card--divider-3::after {
  right: -5.5%;
}

.card--divider-3:nth-child(3n) {
  margin-right: 0;
}

.card--divider-3:nth-child(3n)::after {
  display: none;
}

@media (min-width: 920px) {
  .card--divider-3:last-child {
    margin-right: 0;
  }
}

.card--divider-3:last-child::after {
  display: none;
}

.info-box__grid {
