  }
}

.info-box--grid-item:last-child {
  margin-right: 0;
}

@media (min-width: 920px) {
  .info-box--grid-item {
    width: 48.2%;
  }
  .info-box--grid-item:nth-child(2n) {
    margin-right: 0;
  }
}

