// A was color: $gray-dark;
.main-menu__link {
  color: $white;
  display: block;
  font-size: 1.2rem;
  line-height: 1.5;
  padding: $space $space-triple $space $space-double;
  text-decoration: none;
  font-weight: bold;

  &--with-sub {
    width: 80%;
  }

  @include breakpoint($main-menu-medium) {
    border-bottom: 7px solid transparent;
    color: $white;
    display: inline-block;
    font-size: 1.1rem;
    padding: $space $space-double $space $space;
    text-transform: none;
    position: relative;
    width: auto;

    &.active,
    &:active,
    &:hover {
      color: $gray-lighter;
      border-bottom: 7px solid $c-secondary;
    }

    // &::after {
    //   color: $white;
    //   display: inline-block;
    //   content: ">";
    // }

    &--sub::after {
      display: none;
    }

    &:hover {
      color: $white;

      &::after {
        color: $gray-light;
      }
    }
  }
}

//
// Sub Navigation Links
//
// A (Nested) was color: $gray-dark;
.main-menu__link--sub {
  color: $white;
  padding-left: $space-triple;
  font-weight: normal;

  @include breakpoint($main-menu-medium) {
    color: $gray-light;
    display: block;
    padding: $space-one-third $space;
  }
}

.main-menu--sub-2 {
  background-color: $gray-lightest;
}

.main-menu__link--sub-2 {
  padding-left: $space-quadruple;
}
